import { Component } from '@angular/core';
import { IonicPage, NavController, TextInput, LoadingController, Events, AlertController } from 'ionic-angular';
import { SocketServiceProvider } from '../../providers/socket-service/socket-service';

@IonicPage()
@Component({
  selector: 'page-connect-mobile',
  templateUrl: 'connect-mobile.html',
})
export class ConnectMobilePage {

  constructor(public navCtrl: NavController, private ssp: SocketServiceProvider,
    private loadCtrl: LoadingController, private events: Events, private alertCtrl: AlertController) {
    this.events.subscribe('socket-service : connect-mobile : connected', () => {
      this.navCtrl.setRoot('LandingPage');
    });
  }

  connectToServer(ip: TextInput) {  
    this.ssp.connectToSocketServer(ip.value);
    let loading = this.loadCtrl.create({
      content: 'Connecting...',
      dismissOnPageChange: true
    });
    loading.present();

    loading.onDidDismiss(() => {
      loadCompelete = true;
    });

    let loadCompelete = false;
    setTimeout(() => {
      loading.dismiss();
      if (loadCompelete == false) {
        this.alertCtrl.create({
          title: 'Connection Timed Out',
          message: 'Your connection was timed out. Please check the entered ip address'
        }).present();
        loadCompelete = true;
      }
    }, 5000);
  }
}



